.input {
  padding: 1em;
  width: 100%;
  outline: none;
  background: #fff;
  box-sizing: border-box;
  border: solid 1px #e0e0e0;
  border-radius: 4px;
  will-change: all;
  -webkit-transition: all ease-out 0.3s;
  transition: all ease-out 0.3s; }
  .input:focus, .input:active {
    border-color: #6ec0e6;
    background: #f1f9fd; }
  .input:hover {
    border-color: #43addf; }
