.tabs {
  position: relative;
  box-sizing: border-box; }

.labels {
  display: flex;
  padding: 10px 0; }

.labelItem {
  margin: auto;
  background: transparent;
  border: none;
  outline: none;
  text-align: left;
  cursor: pointer;
  line-height: 1.6;
  color: #999;
  font-family: "PostGrotesk-Bold", sans-serif;
  font-size: 0.875rem; }
  .labelItem_step {
    display: block; }
  .labelItem__active {
    color: #6ec0e6; }
  .labelItem:hover, .labelItem:active, .labelItem:focus {
    color: #43addf; }
  .labelItem:disabled, .labelItem:disabled:hover, .labelItem:disabled:active, .labelItem:disabled:focus {
    cursor: default; }

.contentWrapper_divider {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 4px;
  border-radius: 8px;
  margin-bottom: 48px;
  background: #d4ecf8; }
  .contentWrapper_divider_progress {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 0;
    background: #6ec0e6;
    will-change: width;
    -webkit-transition: width ease-out 0.3s;
    transition: width ease-out 0.3s; }
