.container {
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  border: solid 1px #e0e0e0;
  border-radius: 4px;
  padding: 0;
  overflow: hidden;
  background: #fbfbfb; }

.prefix,
.number {
  display: inline-block;
  height: 100%;
  padding: 0.4em 0.4em 0.1em; }

.prefix {
  background: #ededed; }
