.App {
  min-height: 100vh; }

.header {
  position: sticky;
  max-height: 80px; }

.body {
  max-width: 1100px;
  padding: 0;
  margin: auto; }
  .body_main, .body_aside {
    padding: 32px 32px; }
  .body_main {
    background: #fff;
    border-bottom: 2px solid #e0e0e0; }
  .body_aside {
    background: #fbfbfb; }
  @media (min-width: 992px) {
    .body {
      height: calc(100vh - 80px);
      position: relative; }
      .body_main, .body_aside {
        display: table-cell;
        padding: 60px 32px 32px;
        min-height: 100%; }
      .body_main {
        width: calc(100% - 400px);
        padding-right: 48px;
        border-bottom: 0;
        border-right: 2px solid #e0e0e0; }
      .body_aside {
        width: 400px;
        padding-left: 48px; } }

.content {
  display: inline-table;
  width: 100%;
  height: 100%; }

@media (min-width: 992px) {
  .summary {
    position: sticky;
    top: 100px; } }
